@import url(https://fonts.googleapis.com/css2?family=Lora&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

header{
  background-image: url(/ecommerce-bakeshop/static/media/header.4299d1a5.jpg);
  background-size: cover;
  color: white;
  position: relative;
}
.overlay{
  position: absolute;
  top: 0px;
  left: 0px;
  right: 0px;
  width: 100%;
  height: 100%;
  z-index: 2;
  background-color: rgb(30, 30, 30);
  opacity: .5; 
}
nav{
  display: flex;
  position: relative;
  z-index: 10;
}
.logo{
  width: 50px;
  max-width: 100%;
  height: auto;
  margin: 10px 20px;
}
.company-name{
  line-height: 75px;
  font-size: 28px;
}
.navList{
  position: absolute;
  right: 70px;
  top: 25px;
}
.navLink{
  text-decoration: none;
  font-size: 20px;
  margin: 0px 20px;
  font-weight: bolder;
  color: white;
}
.navLink ion-icon{ margin-right: 5px; }
.navQuantity{ margin: 0px 5px; }

  /*Open and Close Nav Menu*/
.topnav.responsive { position: relative; }
.topnav.responsive .menu {
  position: absolute;
  right: 20px;
  top: 20px;
}
.topnav.responsive .navLink {
  display: block;
  text-align: center;
  padding: 20px;
}
.topnav.responsive .navList{ 
  font-size: 25px;
  position: absolute;
  left: 0;
  right: 0;
  margin: 40px auto;
  background-image: url(/ecommerce-bakeshop/static/media/header.4299d1a5.jpg);
  background-size: cover;
  background-position: center;
}

/* 3. Hamburger menu in mobile responsive*/
header button{
  height: 25px;
  width: 25px;
  background: none;
  border: none;
  position: absolute;
  right: 20px;
  top: 20px;
  }
button:focus{ outline: none; }
.menu-line{
  display: block;
  position: relative;
}
.menu-line,
.menu-line::before,
.menu-line::after{
  width: 25px;
  height: 3px;
  background-color: white;
  transition: transform 250ms ease-in-out;
}
.menu-line::before,
.menu-line::after{
  content: "";
  position: absolute;
  left: 0;
}
.menu-line::before{ bottom: 6px; }
.menu-line::after{ top: 6px; }

/* 4. Hamburger menu animation (menu to x)*/
.topnav.responsive .menu{ transform: rotate(45deg); }
.topnav.responsive .menu-line::before{ opacity: 0; }
.topnav.responsive .menu-line::after{
  transform: rotate(90deg) translate(-6px);
}

@media(min-width: 1000px){
  .menu{ display: none; }
  .logo{ margin-left: 100px; }
}
@media(max-width: 1000px){
  .topnav .navLink { display: none; }
}
